/****** modal  *******/

#modal-w{
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: #000;
  z-index: 9999;
  display: none;
}
#modal-w .modal-inner{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#modal-w .modal-inner .menu li{
  padding: 12px 0 ;
  text-align: center;
}
#modal-w .modal-inner .menu li a{
  color: white;
  font-size: 22px;
}
#modal-w .btn-header{
  width: 36px;
  height: 36px;
  position: absolute;
  top: 30px;
  right: 30px;
}
#modal-w .btn-header .line{
  position: absolute;
  left: 0;
  height: 4px;
  border-radius: 2px;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  background: #fff;
}
#modal-w .btn-header .line:nth-child(1){
  transform: rotate(-45deg);
}
#modal-w .btn-header .line:nth-child(2){
  transform: rotate(45deg);
}




